<!--同步课堂tree-->
<template>
  <el-tree
    class="sync-tree"
    :data="treeData" 
    :props="defaultProps" 
    @node-click="handleNodeClick">
  </el-tree>
</template>

<script>
export default {
  props:{
    treeData:{
      type:Array
    },
    defaultProps:{
      type:Object,
      default:function(){
        return {
          children: 'children',
          label: 'label',
          value:'value'
        }
      }
    }
  },
  methods:{
    handleNodeClick(data){
      if(!data.next) {
        this.$emit("selectSchool",{schoolId:data.id,schoolValue:data.displayName})
      }
    }
  }
}
</script>

<style lang="scss">
.sync-tree.el-tree {
  background: #fff;
}
</style>